import React from 'react';
import { Checkbox } from 'antd';
import { CheckboxOptionType}  from 'antd';

export const PartyCheckbox: React.FC = () => {
    // TODO: 
    const parties: CheckboxOptionType<string>[] = [
        { label: '', value: 'zimintoshigidan' },
        { label: '', value: 'ishin' },
        { label: '', value: 'rikken' },
        { label: '', value: 'kokumin' },
        { label: '', value: 'komei' },
        { label: '', value: 'sansei' },
        { label: '', value: 'reiwa' },
        { label: '', value: 'kyousan' },
        { label: '', value: 'hosyu' },
        { label: '', value: 'shamin' },
        { label: '', value: 'mirai' },
        { label: '', value: 'musyozoku' },
    ];

    return (
        <Checkbox.Group options={parties} />
    );
}