export const CouncillorList: React.FC = () => {
  return (
    /* TODO:  */
    <List
      itemLayout="horizontal"
      dataSource={data}
      renderItem={councillor  => (
        <List.Item>
          <List.Item.Meta
            avatar={councillor.avatar}
            title={<a href={councillor.url}>{councillor.title}</a>}
            description={
              <div>
                {councillor.description}
                <List
                  itemLayout="horizontal"
                  dataSource={councillor.agendas}
                  renderItem={agenda => (
                    <List.Item>
                      <List.Item.Meta
                        avatar={agenda.avatar}
                        title={<a href={agenda.url}>{agenda.title}</a>}
                        description={agenda.description}
                      />
                    </List.Item>
                  )}
                />
              </div>
            }
          />
        </List.Item>
      )}
    />
  )
};